@import url('https://fonts.googleapis.com/css2?family=Inter:wght@200;300;500&display=swap');

/* Offering full page access */
.offering-main-container{
    background-color: #ffffff;
    font-family: 'Inter', sans-serif;
    margin-top: 58px;
}

/* Section - 1 */
.offering-section-1{
    display: flex;
    flex-flow: column wrap;
    align-items: center;
}

.offering-section-1-heading{
    margin: 30px;
    font-size: 40px;
    font-weight: 300;
}

.offering{
    margin-top: 58px;
}
.Incubation{
    margin-left: 40px;
}
.main_heading{
    display: flex;
    flex-direction: row;
}
.heading-text{
    padding: 30px;
    flex: 50%;
    max-width: inherit;
}
@media(max-width: 950px){
    .main_heading{
        flex-direction: column;
    }
}
@media (min-width: 700px) {
    .main_heading,
    .main_heading-lg,
    .main_heading-md,
    .main_heading-sm,
    .main_heading-xl {
      max-width: max-content;
    }
  }
@media (max-width:767px) {
    .img-contain{
        max-width: 100%;
        max-height: 100%;
        flex-direction: column;
        padding: auto;
    }
}
.img-contain{
    padding: 50px;
    flex: 50%; 
} 
