/* Imported font from Google Font */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@200;300;500&display=swap');

/* Full Home section Access Container */
.home-section-container {
  font-family: 'Inter', sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  margin-top: 58px;
}

/* Hero Section  */
.home-section-1-container {
  display: flex;
  flex-wrap: wrap;
  height: 90vh;
  /* margin: 5px; */
  margin-top: 10px;
  box-sizing: border-box;
  padding: 10px;
}

/* Left side Container of herosection */
.section-1-text {
  display: flex;
  padding: 2px;
  margin: 4px;
  flex-flow: column wrap;
  width: 60%;
  align-items: center;
}

/* Left Container Heading (MII Foundation) */
.section-1-heading {
  display: flex;
  flex-wrap: wrap;
  padding: 5px;
  margin: 5px;
  height: 50%;
  /* border: 2px solid black; */
}

/* MII Slogan Container */
.section-1-slogan {
  display: flex;
  flex-wrap: wrap;
  padding-top: 10px;
  height: 28%;
  justify-content: center;
  align-items: center;
  /* border: 2px solid red; */
}

/* Styling (MII Foundation) h3 tag */
.section-1-home-container1 {
  display: flex;
  align-items: flex-end;
  font-size: 90px;
  font-weight: bold;
  text-align: center;
}

/* Right Side Container of herosection */
.section-1-image {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 35%;
  padding: 2px;
  margin: 4px;
}

.hero-section-1-img {
  /* width: 100%; */
  height: 80%;
}

/* Styling (MII Slogan) h3 tag*/
.mii-slogan {
  font-size: 30px;
  text-align: center;
}

/* Home Section (second) Container after herosection */
.section2-home-page {
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 2px solid red; */
}

/* Container inside second container */
.section2-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 92vh;
  width: 94%;
  /* border: 2px solid green; */
}

/* Left container inside section-2-container */
.section2-heading {
  width: 30%;
  margin: 10px;
  padding: 8px;
  /* border: 2px solid rgb(22, 30, 22); */

}

.section2-heading h1 {
  color: white;
  /* border-right: 3px dotted #017FE4; */
  border-right: 3px solid #ffffff;
  font-size: 35px;
  letter-spacing: 1px;
  line-height: 1.4;
  padding: 1% 3%;
  /* text-align: center; */
}

/* Right container inside section-2-container */
.section2-content {
  width: 60%;
  margin: 10px;
  padding: 8px;
}

.section2-content p {
  color: white;
  font-size: 20px;
  letter-spacing: 1px;
  line-height: 1.3;
  /* text-align: center; */
}